import slide from 'components/slide/index'
import http from 'js/http'

export default {
  name: "index",
  components: {
    slide
  },
  data() {
    return {
      nodeInfo: {},
      replaceStr:function (str) {
        if (!str) return ''
        return str.replace(/\r\n/g, '<br/>').replace(/\n/g, '<br/>').replace(/\s/g, '&nbsp')
      }
    }
  },
  computed: {
    bgStyle() {
      let id = this.nodeInfo.typeId
      if (!id) id = 1
      return {
        backgroundImage: "url(/static/images/personal-node/bg_" + id + ".jpg)"
      }
    }
  },
  methods: {
    goVoting() {
      this.$router.push({
        path: `/home/node/dts${this.$route.params.id}/voting`
      })
    },
    getData() {
      http.post('/node/info', {
        id: this.$route.params.id,
      }, (res) => {

        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.nodeInfo = res.content
        // console.log(this.noticeInfo)
      })
    }
  },
  created() {
    this.getData()
  },
  /*beforeRouteLeave (to, from, next) {
    /!*if (this.flag) {//这是一个点击事件
      //说明我是点击事件的跳转
      next(); //正常执行手机返回键也是正常返回上一个路由
    } else {
      if (from.path !== 'site') { //要离开的路由不是site
        next();//
      }
      //说明我是返回事件的跳转
      //next(false);
      this.$router.push({path: 'site'})//返回键要返回的路由
    }*!/
  },*/
}



